@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  font-family: 'montserrat light';
}

.fixedTop {
  box-shadow: 0px 3px 4px 0px #22232444;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0em 2em;
  height: 3.5em;
  z-index: 100;
}

.fixedTop .pageTitle {
  font-family: 'montserrat';
  font-size: 1.1rem;
}

.fixedTop .burger {
  width: 2.6em;
  height: 3.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  cursor: pointer;
}

.fixedTop .menu {
  width: 90%;
  height: 1px;
  background-color: #021a31;
  position: relative;
  pointer-events: none;
  transition: all .5s ease;
}

.fixedTop .menu::before, .fixedTop .menu::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #021a31;
  border-radius: 12px;
  transition: all .5s ease;
}

.fixedTop .menu::before {
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

.fixedTop .menu::after {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.fixedTop .user {
  width: 1.6em;
  cursor: pointer;
}

.fixedTop .search {
  display: block;
  cursor: pointer;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  right: 0;
}

.fixedTop .showNav .menu {
  width: 80%;
}

.fixedTop .showNav .menu::before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.fixedTop .showNav .menu::after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.fixedTop .inputwrapper {
  width: 0px;
  height: 2em;
  background: #def2ff70;
  border-radius: 20px;
  position: relative;
  transition: all .2s linear;
}

.fixedTop form input {
  margin: 0;
  padding: 0 1em;
  display: inline;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
  outline: none;
  background: transparent;
  color: darkgray;
  font-family: 'montserrat light';
  font-style: italic;
}

.fixedTop .showSearchBar {
  width: 15em;
}

@media (max-width: 700px) {
  .fixedTop .pageTitle {
    font-size: .9rem;
  }
  .fixedTop .burger {
    width: 2.5em;
    height: 2.8 em;
  }
  .fixedTop .user, .fixedTop .search {
    width: 1.3em;
  }
  .fixedTop .menu::before {
    -webkit-transform: translateY(8px);
            transform: translateY(8px);
  }
  .fixedTop .menu::after {
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
  }
  .fixedTop .inputwrapper {
    height: 1.8em;
  }
  .fixedTop .showSearchBar {
    width: 12em;
  }
}

.fixedTop .seaching {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: auto;
  background: white;
  font-style: italic;
  font-size: .8em;
  color: gray;
  padding: 3em .4em 2em;
  z-index: -1;
}

@media (max-width: 630px) {
  .fixedTop .pageTitle {
    font-size: .8rem;
  }
}

@media (max-width: 510px) {
  .fixedTop .pageTitle {
    display: none;
  }
  .fixedTop .inputwrapper {
    margin: 0px 2em 0em auto;
  }
  .fixedTop .user {
    width: 1.5em;
  }
}

.nav {
  height: calc( 100vh - 3.5em);
  position: fixed;
  top: 3.5em;
  background: white;
  left: 0;
  box-shadow: 3px 2px 4px 0px rgba(21, 57, 105, 0.267);
  z-index: 10;
  width: 400px;
  max-width: 100%;
  padding: 2.3em;
  margin: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all .2s ease;
}

.nav li {
  list-style: none;
  padding: 0 0 .7em 1em;
  margin-bottom: .7em;
  transition: all .2s ease;
}

.nav li:first-of-type {
  border-bottom: 1.4px solid #021a3159;
}

.nav a {
  text-decoration: none;
  display: block;
  color: #021a31;
  font-size: 1.3rem;
  width: 100%;
}

.nav li:hover {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

.toggleOpen {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.main * {
  z-index: -2;
}

.landingWrapper {
  height: calc(100vh - 3.5em);
  position: relative;
  background: linear-gradient(to right, #021a3180, #021a3180), url(/military/static/media/landing.76558e35.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-align: center;
}

.landingWrapper h1 {
  font-size: clamp(1.7rem, 4vw, 2.6rem);
}

.landingWrapper p {
  padding: 2em 2em;
  font-size: clamp(1rem, 2.2vw, 1.4rem);
}

.landingWrapper button {
  outline: none;
  border: none;
  background: #0d3d85;
  font-size: clamp(1rem, 3vw, 1.3rem);
  padding: .8em 1.8em;
  border-radius: 3rem;
  color: white;
}

.desc1, .desc2 {
  background: url(/military/static/media/pixabay.1e488fc8.jpg) no-repeat center;
  background-size: cover;
  color: white;
  min-height: 40vh;
  height: auto;
}

.desc1 .text, .desc2 .text {
  width: 70%;
  height: 100%;
  background: #021a3181;
  text-align: center;
  padding: 4em;
}

.desc1 h1, .desc2 h1 {
  font-size: clamp(1.7rem, 4vw, 2.6rem);
  padding: 0em 0em 1em;
}

@media (max-width: 630px) {
  .desc1 .text, .desc2 .text {
    font-size: 1rem;
    width: 100%;
  }
}

.desc2 {
  background: url(/military/static/media/pixabaySecond.0ac472d0.jpg) no-repeat center;
  background-size: cover;
  height: 40vh;
}

.desc2 .text {
  float: right;
}

footer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[12];
      grid-template-columns: repeat(12, 1fr);
  background: #045aaa;
  color: white;
  padding: 2em;
}

footer ul {
  margin: 0;
  padding: 0;
}

footer ul li {
  list-style-type: none;
  padding: .5em 0em;
}

footer ul a {
  color: white;
  text-decoration: none;
}

footer > div {
  grid-column: auto / span 3;
}

.newsLandingWrapper {
  display: flex;
}

.newsLandingWrapper .advertMain {
  height: 60vh;
  width: 100%;
  max-width: 48em;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  overflow: hidden;
}

.newsLandingWrapper .mainImg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}

.newsLandingWrapper button {
  outline: none;
  border: none;
  background: #0d3d85;
  font-size: clamp(0.9rem, 3vw, 1.3rem);
  padding: .6em 1.8em;
  border-radius: 3rem;
  color: white;
}

.newsLandingWrapper h1 {
  text-align: center;
  font-size: clamp(1.2rem, 5vw, 1.8rem);
  padding: 1em;
}

.newsLandingWrapper .dark {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #021a318c;
  z-index: -1;
}

@media (max-width: 535px) {
  .newsLandingWrapper .advertMain {
    height: 50vh;
  }
}

@media (max-width: 470px) {
  .newsLandingWrapper .advertMain {
    height: 30vh;
  }
}

.galaryOne {
  padding: 1em;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(300px, 1fr))[auto-fit];
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.galaryOne > div {
  background-color: #f3f3f3;
  box-shadow: 3px 3px 5px 0px #052d5a63;
  margin: .6em;
}

.galaryOne .imgWrapper {
  height: 13em;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.galaryOne .imgWrapper img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.galaryOne .desc {
  padding: .8em 1em;
  text-align: center;
  color: #1d4b8f;
  font-weight: bold;
}

.advert {
  width: 100%;
  max-width: 450px;
  margin: .9em;
  display: flex;
}

.advert .newsItem {
  background: #f3f3f3;
  padding: 1.2em;
  width: 70%;
  border-radius: .8em 0em 0em .8em;
  overflow: hidden;
}

.advert h3 {
  font-size: clamp(0.6rem, 2.4vw, 0.95rem);
  padding-bottom: .3em;
  white-space: nowrap;
}

.advert p {
  font-size: .85rem;
  white-space: nowrap;
}

.advert .wrapper {
  width: 20%;
  position: relative;
  border-radius: 0em .8em .8em 0em;
  position: relative;
  overflow: hidden;
}

.advert .wrapper img {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  -webkit-transform: translate(10%, 0%);
          transform: translate(10%, 0%);
}

.advert .wrapper::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #f3f3f3, 30%, #03030328);
  z-index: 2;
}

@media (max-width: 1000px) {
  .newsLandingWrapper {
    display: block;
  }
  .newsLandingWrapper .advertMain {
    max-width: 100%;
  }
  .newsLandingWrapper .wrapper {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (minmax(250px, 1fr))[auto-fit];
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  .newsLandingWrapper .wrapper .advertSecondary {
    padding: clamp(0.3em, 3vw, 0.8em);
  }
  .newsLandingWrapper .wrapper .advert {
    width: 100%;
    padding: 0;
    margin: 0;
  }
  .newsLandingWrapper .advert {
    margin: 1.2em 0;
  }
}
